.center-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

.page-body {
  font-family: "Poppins Medium", "Arial", sans-serif;
  font-size: 16px;
  line-height: 21px;
  color: #1d1613;
  height: 100%;
}

.page-header {
  margin: 0 0 23px;
  padding: 17px 50px 0;
  z-index: 2;
}
@media (max-width: 1023px) {
  .page-header {
    padding: 27px 45px 0;
  }
}
@media (max-width: 767px) {
  .page-header {
    padding: 10px 15px 0;
    margin: 0 0 6px;
  }
}

.page-header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1168px;
  margin: 0 auto 26px;
  padding: 0;
}
@media (max-width: 1023px) {
  .page-header__inner {
    margin: 0 auto 5px;
  }
}

.page-header__search-block {
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .page-header__search-block {
    display: none;
  }
}

.page-header__search-inner {
  position: relative;
  margin: 0;
  padding: 0;
}
.page-header__search-inner::before {
  content: "";
  position: absolute;
  top: 3px;
  left: 0;
  background: url("../img/decor/magnifier.svg") no-repeat;
  background-size: 14px;
  width: 15px;
  height: 15px;
}

.page-header__search-block input {
  border: 0;
  padding: 0 0 0 30px;
}
.page-header__search-block input:hover {
  outline: 1px solid black;
  transition: 0.2s;
}
.page-header__search-block input::placeholder {
  color: #9b9997;
  font-size: 14px;
  line-height: 21px;
}

.page-header__logo {
  z-index: 1;
  transform: translate(-58px, 13px);
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .page-header__logo {
    transform: translate(-5px, 3px);
  }
}

.page-header__logo svg {
  width: 66px;
  height: 66px;
  transform: rotate(-360deg);
  transition: 0.5s;
}
@media (max-width: 767px) {
  .page-header__logo svg {
    transform: scale(0.8);
  }
}

@media (max-width: 1023px) {
  .page-header__logo.page-header__logo--change-color svg {
    stroke: white;
    transform: rotate(360deg);
    transition: 0.5s;
  }
}
@media (max-width: 767px) {
  .page-header__logo.page-header__logo--change-color svg {
    transform: scale(0.8) rotate(360deg);
  }
}

.page-header__login-block {
  display: flex;
  z-index: 2;
  margin: 0;
  padding: 0;
}
.page-header__login-block:hover {
  transform: scale(1.2);
  transition: 0.2s;
}

@media (max-width: 1023px) {
  .page-header__login-block.page-header__login-block--change-bag a:last-child::before {
    background-color: white;
    transition: 1s;
  }
}

.page-header__login-block p {
  font-size: 14px;
  line-height: 21px;
  margin: 0;
  padding: 0;
}
.page-header__login-block p:first-child {
  margin-right: 35px;
}
@media (max-width: 1023px) {
  .page-header__login-block p:first-child {
    display: none;
  }
}

.page-header__login-block a {
  color: #1d1613;
}
.page-header__login-block a:hover {
  color: #a87b62;
  transition: 0.2s;
}
.page-header__login-block a:active {
  opacity: 0.6;
}
@media (max-width: 1023px) {
  .page-header__login-block a:last-child {
    display: block;
    font-size: 0;
    line-height: 0;
  }
  .page-header__login-block a:last-child::before {
    content: "";
    display: block;
    -webkit-mask: url("../img/decor/bag.svg") no-repeat 50% 50%;
    mask: url("../img/decor/bag.svg") no-repeat 50% 50%;
    -webkit-mask-size: cover;
    mask-size: cover;
    background-size: 18px 16px;
    width: 18px;
    height: 16px;
    background-color: #1d1613;
    transition: 1s;
  }
}

.page-header__tablet {
  display: none;
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .page-header__tablet {
    justify-content: space-between;
    align-items: center;
  }
}

@media (max-width: 1023px) {
  .page-header__tablet-disappear {
    display: flex;
  }
}

@media (max-width: 1023px) {
  .page-header__tablet-inner {
    margin: 0;
    padding: 21px 0 0 21px;
  }
}
@media (max-width: 767px) {
  .page-header__tablet-inner {
    padding: 20px 0 0 17px;
  }
}

.page-header__tablet-inner svg {
  background-size: 68px 21px;
  width: 68px;
  height: 21px;
}

.navigation {
  border-top: 1px solid #f6f5f4;
  margin: 0;
  padding: 18px 0 0 0;
}
@media (max-width: 1023px) {
  .navigation {
    display: none;
  }
}

.navigation ul {
  display: flex;
  max-width: 481px;
  list-style: none;
  margin: 0 auto;
  padding: 0;
}

.navigation li {
  white-space: nowrap;
  margin: 0 35px 0 0;
  padding: 0;
}
.navigation li:last-child {
  margin: 0;
}

.navigation a {
  color: #1d1613;
}
.navigation a:hover {
  color: #a87b62;
  transition: 0.2s;
}
.navigation a:active {
  opacity: 0.6;
}

.burger-closed {
  display: none;
  cursor: pointer;
  transition: 1s all;
  z-index: 2;
}
@media (max-width: 1023px) {
  .burger-closed {
    display: none;
    justify-content: center;
    align-items: center;
    min-width: 25px;
    min-height: 16px;
    background-color: transparent;
    margin: 0;
    padding: 0;
  }
}
@media (max-width: 767px) {
  .burger-closed {
    min-width: 25px;
    min-height: 16px;
  }
}

.burger-closed button {
  display: block;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  margin: 0;
  padding: 6px 0 0;
}
.burger-closed button:hover {
  transform: scale(1.1);
  transition: 0.2s;
}
.burger-closed button::before {
  content: "";
  display: block;
  border-bottom: 2px solid #1d1613;
  width: 23px;
  padding: 0;
  margin: 0 0 7px 0;
  transition: 1s;
}

@media (max-width: 1023px) {
  .burger-closed.burger-closed--change-color button::before {
    border-bottom: 2px solid white;
  }
}

.burger-closed span::before {
  content: "";
  display: block;
  border-bottom: 2px solid #1d1613;
  width: 23px;
  padding: 0;
  margin: 0 0 7px 0;
  transition: 1s;
}
.burger-closed span::after {
  content: "";
  display: block;
  border-bottom: 2px solid #1d1613;
  width: 23px;
  padding: 0;
  margin: 0 0 7px 0;
  transition: 1s;
}

@media (max-width: 1023px) {
  .burger-closed.burger-closed--change-color span::before {
    border-bottom: 2px solid white;
  }
  .burger-closed.burger-closed--change-color span::after {
    border-bottom: 2px solid white;
  }
}

.burger-closed--change-display {
  display: flex;
}

.burger-closed--background-color {
  background-color: transparent;
}

.burger-open {
  display: none;
  z-index: 1;
}
@media (max-width: 1023px) {
  .burger-open {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    min-height: 500%;
    transition: 1s;
    background-color: #a87b62;
  }
}

.burger-open--appear {
  display: block;
  padding: 135px 45px 0;
}
@media (max-width: 767px) {
  .burger-open--appear {
    padding: 135px 15px 0;
  }
}

.burger-open__list {
  margin: 0;
  padding: 0;
}

.burger-open__list a {
  color: white;
}

.burger-open__list-item a {
  display: block;
  font-size: 20px;
  line-height: 28px;
  color: #9b9997;
  padding: 15px;
}

.burger-open__action {
  display: none;
}

.burger-open__search {
  position: relative;
  margin: 0 0 42px;
  padding: 0;
}
@media (max-width: 1023px) {
  .burger-open__search::before {
    content: "";
    position: absolute;
    top: 11px;
    left: 11px;
    -webkit-mask: url("../img/decor/magnifier.svg") no-repeat 50% 50%;
    mask: url("../img/decor/magnifier.svg") no-repeat 50% 50%;
    -webkit-mask-size: cover;
    mask-size: cover;
    background-size: 18px 17px;
    width: 18px;
    height: 17px;
    background-color: white;
  }
}
@media (max-width: 767px) {
  .burger-open__search {
    margin: 0 0 34px;
  }
}

.burger-open__search input {
  min-width: 100%;
  min-height: 40px;
  background-color: white;
  opacity: 0.15;
  border: 0;
  outline: 0;
  border-radius: 21px;
  padding: 0 0 0 50px;
}

.burger-open__list-nav {
  margin-bottom: 37px;
}
@media (max-width: 767px) {
  .burger-open__list-nav {
    margin-bottom: 26px;
  }
}

.burger-open__list-nav p {
  margin: 0 0 22px;
  padding: 0;
}
.burger-open__list-nav p:last-child {
  margin: 0;
}

.burger-open__list-nav a {
  font-family: "Poppins Medium", "Arial", sans-serif;
  font-size: 16px;
  line-height: 21px;
  color: #1d1613;
  font-size: 24px;
  line-height: 36px;
  color: white;
}
.burger-open__list-nav a:hover {
  opacity: 0.4;
}
.burger-open__list-nav a:active {
  opacity: 0.2;
}

.burger-open__list-contacts {
  margin-bottom: 42px;
}
@media (max-width: 767px) {
  .burger-open__list-contacts {
    margin-bottom: 34px;
  }
}

.burger-open__list-contacts p {
  margin: 0 0 19px;
  padding: 0;
}
.burger-open__list-contacts p:last-child {
  margin: 0;
}

.burger-open__list-contacts a {
  font-family: "Poppins Medium", "Arial", sans-serif;
  font-size: 16px;
  line-height: 21px;
  color: #1d1613;
  font-size: 18px;
  line-height: 25px;
  opacity: 0.75;
  color: white;
}
.burger-open__list-contacts a:hover {
  opacity: 0.4;
}
.burger-open__list-contacts a:active {
  opacity: 0.2;
}

.burger-open__list-login p {
  margin: 0;
  padding: 0;
}

.burger-open__list-login a {
  font-family: "Poppins Medium", "Arial", sans-serif;
  font-size: 16px;
  line-height: 21px;
  color: #1d1613;
  font-size: 18px;
  line-height: 24px;
  color: white;
}
.burger-open__list-login a:hover {
  opacity: 0.4;
}
.burger-open__list-login a:active {
  opacity: 0.2;
}

.page-main {
  flex: 1;
  margin: 0;
  padding: 0;
}

.main-block {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: url("../img/desktop/main/background/main-block.png") no-repeat;
  background-size: cover;
  background-position: center;
  min-height: 463px;
  width: 100%;
  background-color: #a87b62;
  margin: 0 0 119px;
  padding: 0;
}
@media (max-width: 1023px) {
  .main-block {
    background: url("../img/tablet/main/background/main-block.png") no-repeat;
    background-size: cover;
    margin: 0 0 71px;
  }
}
@media (max-width: 767px) {
  .main-block {
    background: url("../img/mobile/main/background/main-block.png") no-repeat;
    background-size: cover;
    margin: 0 0 62px;
  }
}

.main-block h3 {
  font-family: "Cormorant Garamond Bold", "Arial", sans-serif;
  font-size: 45px;
  line-height: 59px;
  color: white;
  line-height: 51px;
  letter-spacing: 0.03em;
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .main-block h3 {
    padding: 5px 16px 0 0px;
  }
}
@media (max-width: 767px) {
  .main-block h3 {
    font-size: 38px;
    line-height: 49px;
    padding: 1px 0 0;
  }
}

.main-block p {
  font-family: "Cormorant Garamond Medium Italic", "Arial", sans-serif;
  font-size: 45px;
  line-height: 59px;
  color: white;
  font-size: 38px;
  line-height: 49px;
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .main-block p {
    padding: 0px 16px 0 0px;
  }
}
@media (max-width: 767px) {
  .main-block p {
    font-size: 34px;
    line-height: 44px;
    padding: 0;
  }
}

.new-entries {
  margin: 0 0 115px;
  padding: 0 45px;
}
@media (max-width: 1023px) {
  .new-entries {
    margin: 0 0 65px;
    padding: 0 25px;
  }
}
@media (max-width: 767px) {
  .new-entries {
    margin: 0 0 55px;
  }
}

.new-entries__inner {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  max-width: 1166px;
  min-width: 0;
  overflow: hidden;
  margin: 0 auto;
  padding: 0;
}
